import React from 'react'

const ShowContacts = (props) => {
    return(
      <ul>
      {props.persons.map((person, i) =>
        <p key={i}>{person.name} {person.number} <button onClick={() => props.handleDeleteClick(person.id)}>delete</button></p>
      )}
      </ul>
    )
  }

export default ShowContacts